.navbar {
    position: fixed;
    z-index: 100;

    display: flex;

    justify-content: center;
    align-items: center;
    transition: all 300ms ease-in-out;
}

.navbar-elevated {
    background-color: white;
    box-shadow: 0 3px 6px rgba(0,0,0,0.10);
}

.navbar-button {
    color: white;
    
    text-transform: uppercase;

    background-color: transparent;
    border: none;
    
    cursor: pointer;
    transition: all 300ms ease-in-out;
}

.navbar-button:focus {
    outline:0;
}

.navbar-button-elevated {
    color: black;
}

.navbar-link {
    background-color: transparent;
    transition: all 300ms ease-in-out;
}

.navbar-link:visited, .navbar-link:hover, .navbar-link:active {
    color: white;
    text-decoration: none;
}

.navbar-link:hover {

    background-color: rgba(0, 0, 0, 0.05);
}
