.sample-app-logo {
    width: "32px";
    height: "32px";
    margin-right: "8px";
    
    color: #24292e;
    fill: currentColor;
}

.sample-app-button {
    width: 270px;

    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 16px;
    margin-right: 16px;

    margin-top: 8px;

    border-radius: 10px;
    border: 2px solid #24292e;

    background-color: transparent;

    color: #24292e;
    text-align: center;
    text-decoration: none;
    font-size: 18px;
    font-weight: 500;

    display: flex;
    flex-direction: row;

    align-items: center;
    transition: all 300ms ease-in-out;
}

.sample-app-button:link, .sample-app-button:hover, .sample-app-button:visited, .sample-app-button:active {
    color: #24292e;
    text-decoration: none;
}

.play-store-logo {
    width: "32px";
    height: "32px";
    margin-right: "8px";
    
    fill: currentColor;

    -webkit-animation: play-store-icon-color 10000ms infinite;
    animation: play-store-icon-color 10000ms infinite;
}

.play-store-button {
    width: 270px;

    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 16px;
    margin-right: 16px;

    margin-top: 8px;

    border-radius: 10px;
    border: 2px solid #4CAF50;

    background-color: transparent;

    color: #4CAF50;
    text-align: center;
    text-decoration: none;
    font-size: 18px;
    font-weight: 500;

    display: flex;
    flex-direction: row;

    align-items: center;

    -webkit-animation: play-store-button-border-change 10000ms infinite;
    animation: play-store-button-border-change 10000ms infinite;
    transition: all 300ms ease-in-out;
} 

.play-store-button:link, .play-store-button:hover, .play-store-button:visited, .play-store-button:active {
    text-decoration: none;
}

.play-store-button:hover, .sample-app-button:hover {
    background-color: rgba(0, 0, 0, 0.05);
}

@-webkit-keyframes play-store-button-border-change {
    0% { border: 2px solid #4CAF50; color: #4CAF50; }
    25% { border: 2px solid #03A9F4; color: #03A9F4; }
    50% { border: 2px solid #F44336; color: #F44336; }
    75% { border: 2px solid #E91E63; color: #E91E63; }
    100% { border: 2px solid #4CAF50; color: #4CAF50; }
}

@keyframes play-store-button-border-change {
    0% { border: 2px solid #4CAF50; color: #4CAF50; }
    25% { border: 2px solid #03A9F4; color: #03A9F4; }
    50% { border: 2px solid #F44336; color: #F44336; }
    75% { border: 2px solid #E91E63; color: #E91E63; }
    100% { border: 2px solid #4CAF50; color: #4CAF50; }
}

@-webkit-keyframes play-store-icon-color {
    0% { color: #4CAF50; }
    25% { color: #03A9F4; }
    50% { color: #F44336; }
    75% { color: #E91E63; }
    100% { color: #4CAF50; }
}

@keyframes play-store-icon-color {
    0% { color: #4CAF50; }
    25% { color: #03A9F4; }
    50% { color: #F44336; }
    75% { color: #E91E63; }
    100% { color: #4CAF50; }
}
